                    >
                      <v-list-item-content
                      >
                        <v-list-item-title class="font-weight-medium">{{ item }}</v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </v-tooltip>
                  <v-btn icon color="orange" :href="GetCardImg(v.p_d_adress)" @click="DownAdd(v.p_id)">
                    <font-awesome-icon
                      :icon="['fas', 'download']"
                    ></font-awesome-icon>
                  </v-btn>
                  <v-text class="text-sm-body-1">
                    {{ v.p_downum }}
                  </v-text>
                  <!--                  {{ v.p_downum }}-->
                </v-card-actions>
              </div>
            </v-card>
          </div>
          <div class="next-boot">
            <!--表格-->
            <v-card style="">
              <template>
                <el-table
                  :data="pluglistL"
                  stripe
                  style="width: 100%"
                  height="39vh"
                >
                  <el-table-column prop="p_title" label="名称" width="180">
                  </el-table-column>
                  <el-table-column prop="p_img_adress" label="图片">
                    <template slot-scope="scope">
                      <img
                        :src="GetImageurl(scope)"
                        alt="暂无图片"
