<script>
import MainMenu from "./components/MainMenu";
export default {
  name: "App",

  components: {
    MainMenu
  },

  data: () => ({
    hotdetaillist: [],
    detaillist: [],
    currentmid: "",
    pagesize: "",
    currentpage: "1",
    flist: "",
    hotlist: [],
    defaulturl: "",
    pluglistL: [],
    menulist: [],
    title: "",
    flat: false,
    clipped: true,
    drawer: true,
    mini: true,
    headers: [
      { text: "编号", value: "num" },
      { text: "工具截图", value: "screen" },
      { text: "工具名", value: "p_title" },
      { text: "工具描述", value: "des" },
      { text: "下载量", value: "downum" },
      { text: "下载地址", value: "p_adress" }
    ]
  }),
  methods: {
    async GetMenu () {
      const { data: res } = await this.$http.get("getmenu");
      console.log(res.data);
      this.menulist = res.data;
      this.flist = this.menulist[0].children[0].m_id;
      this.GetMsg(this.flist);
    },
    GetImageurl(data) {
      // console.log(this.$http.defaults.baseURL + data.row.p_img_adress);
      return this.$http.defaults.baseURL + data.row.p_img_adress;
    },
    GetCardImg(data) {
      return this.$http.defaults.baseURL + data;
    },
    GetAIndex(data) {
      console.log(data);
      this.GetMsg(data);
    },
    checkrow(data) {
      console.log(data);
    },
    async GetMsg(mid) {
      if (mid !== "nil") {
        this.currentmid = mid;
      }
      const { data: ress } = await this.$http.get("getmenubyid", {
        params: { mid: this.currentmid }
      });
      const { data: res } = await this.$http.get("getplug", {
        params: { mid: this.currentmid, page: this.currentpage }
      });
      const { data: hotres } = await this.$http.get("gethotpbyid", {
        params: { mid: this.currentmid }
      });
      this.title = ress.data[0].m_name;
      this.hotlist = hotres.data;
      console.log(res.data);
      this.pluglistL = res.data;
      this.pagesize = res.allpage;
      for (let i in res.data) {
        let m = res.data[i].p_content.split(",")
        this.detaillist.push(m);
      }
      for (let ii in hotres.data) {
        let hm = res.data[ii].p_content.split(",")
        this.hotdetaillist.push(hm);
      }
      console.log("ssss", this.hotdetaillist);
    },
    async DownAdd(pid) {
      const { data: res } = await this.$http.get("getdownloadadd", {
        params: { pid: pid }
      });
      if (res.data != "success") {
        this.$message.error("下载失败");
      } else {
        this.$message.success("下载成功");
      }
      this.GetMsg("nil");
    }
  },
  mounted() {
    console.log(this.$http.defaults.baseURL);
    this.GetMenu();
  }
};
</script>
