<template>
  <el-menu
    :default-active="flist"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="#ffd04b"
  >
    <el-submenu v-for="(k, v) in menulist" :key="v" :index="k.mid">
      <template slot="title">
        <!--        <i class="el-icon-location"></i>-->
        <span>{{ k.Tiltle }}</span>
      </template>
      <el-menu-item
        v-for="(ck, cv) in k.children"
        :index="ck.m_id"
        @click="ReturnAcIndex(ck.m_id)"
        :key="cv"
        :id="ck.m_id"
        >{{ ck.m_name }}</el-menu-item
      >
    </el-submenu>
  </el-menu>
</template>

<script>
export default {
  name: "MainMenu",
  methods: {
    ReturnAcIndex(index) {
      this.$emit("func", index);
    }
  },
  props: {
    menulist: [],
    flist:String
  },
  data: function() {
    return {

    };
  }
};
</script>

